// Cast the lighting
const Lighting = () => {
    const { mouse } = useThree();
    const light = useRef();
    
    useFrame(() => {
        light.current.position.set( mouse.current[0] / 20, -mouse.current[1] / 20, 0 )
    });
    
    console.log('mouse --> ', mouse);
    return (
        <>
            <spotLight 
                ref={light} intensity={0.5} position={[10, 10, 40]} penumbra={1} 
            />
        </>
    );
};

// The canvas houses this function, the scene. Scene is where the elements come together.
function SmokePuff({ geometry, material, particleArray }) {
    let ref = useRef();
    let t = Math.random() * 100;
    let speed = 0.01 + Math.random() / 200;
    let factor = 20 + Math.random() * 100;
    let xFactor = -50 + Math.random() * 100;
    let yFactor = -50 + Math.random() * 100;
    let zFactor = -30 + Math.random() * 60;
    
    useFrame(() => {
        t += speed;
        const s = Math.cos(t);
        ref.current.scale.set(s, s, s);
        ref.current.rotation.set(s * 5, s * 5, s * 5);
        ref.current.position.set(
            xFactor +
                Math.cos((t / 30) * factor) +
                (Math.sin(t * 1) * factor) / 10,
            yFactor +
                Math.sin((t / 20) * factor) +
                (Math.cos(t * 2) * factor) / 10,
            zFactor +
                Math.cos((t / 10) * factor) +
                (Math.sin(t * 3) * factor) / 20
        );
    });

    return <mesh ref={ref} material={material} geometry={geometry} />;
}

const GenerateSmoke = ({ particleArray }) => {
    const [geometryRef, geometry] = useResource();
    const [materialRef, material] = useResource();
    const url = 'https://s3-us-west-2.amazonaws.com/s.cdpn.io/95637/Smoke-Element.png';
    const texture = useMemo(() => new THREE.TextureLoader().load(url), [url]);
    
    return (
        <>
            <Lighting />
            
            <planeGeometry 
                ref={geometryRef} 
                attach="geometry"   
                args={[200, 200]} 
            />
            <meshLambertMaterial 
                ref={materialRef}
                attach="material" 
                color="#ccbbee"
                transparent
            >
                <primitive attach="map" object={texture} />
            </meshLambertMaterial>

            {geometry && new Array(3).fill().map((_, index) => (
                <SmokePuff
                    key={index}
                    material={material}
                    geometry={geometry}
                    particleArray={particleArray}
                />
            ))}
        </>
    );
}


// This is the canvas. It's the lowest level element in the three/webGl chain
const CanvasicalSmoke = () => {
    const particleCount = 3;
    const particleArray = [{ id: 0, x: 0, y: 0, z: 2 }];

    useEffect(() => {
        if (particleArray.length === particleCount) {
            return null;
        } else {
            for (let i = 0; i <= particleCount; i++) {
                particleArray.push({
                    id: particleArray[i].id + 1,
                    x: particleArray[i].x + 1,
                    y: particleArray[i].y + 1,
                    z: 2
                });
            }
        }
    }, [particleArray]);

    return (
        <>
            <Canvas
                className="container block__three-container"
                camera={{
                    fov: 75,
                    aspect: 0.5,
                    near: 1,
                    far: 10000,
                    position: [0, 0, 300]
                }}
            >
                <GenerateSmoke particleArray={particleArray} />
            </Canvas>

            {/* <TextLoop  /> */}
        </>
    );
};

export default CanvasicalSmoke;

